import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { Cliente } from './cliente'
import { ClienteService } from './cliente.service';
import { ItemPaginador } from '../utils/itemPaginador'
import swal from 'sweetalert2'

@Component({
  selector: 'app-clientes',
  templateUrl: './clientes.component.html'
})
export class ClientesComponent implements OnInit {

  clientes: Cliente[];
  errores: string[];



constructor(private clienteService: ClienteService, private router: Router) { }

ngOnInit() {
  this.cargarClientes();
}

cargarClientes(){
  this.clienteService.getClientes(this.page, this.size, this.order, this.asc).subscribe(
    rtaJson => {
      this.clientes = rtaJson.clientes

      this.isFirst = rtaJson.paginador.first;
      this.isLast = rtaJson.paginador.last;
      this.totalPages = new Array(rtaJson.paginador.totalPaginas);
      this.numerosFooterPaginador = rtaJson.paginador.paginas
    },
    err => {

      console.error('Codigo de error desde el backend: '+ err.status)
      console.error('Mensaje de error desde el backend: '+ err.message)
    }
  )
}

delete(cliente: Cliente){
  swal({
  title: 'Está seguro?',
  text: `¿Seguro que desea eliminar al cliente ${cliente.nombre}?`,
  type: 'warning',
  showCancelButton: true,
  confirmButtonColor: '#3085d6',
  cancelButtonColor: '#d33',
  confirmButtonText: 'Si, eliminar!',
  cancelButtonText: 'No, cancelar!',
  confirmButtonClass: 'btn btn-success',
  cancelButtonClass: 'btn btn-danger',
  buttonsStyling: false,
  reverseButtons: true
}).then((result) => {
  if (result.value) {

    this.clienteService.delete(cliente.id).subscribe(
      response => {
        this.clientes = this.clientes.filter(cli => cli !== cliente)
        swal(
          'Cliente Eliminado!',
          `Cliente ${cliente.nombre} eliminado con éxito.`,
          'success'
        )
      }
    )

  }
})
}


/* PAGINADOR */
  totalPages: Array<number>;
  page = 0;
  size = 5;
  order = 'nombre';
  asc = true;
  isFirst = false;
  isLast = false;
  numerosFooterPaginador: ItemPaginador[];

  sort(): void {
    this.asc = !this.asc;
    this.cargarClientes();
  }

  rewind(): void {
      if (!this.isFirst) {
        this.page--;
        this.cargarClientes();
      }
    }

    forward(): void {
      if (!this.isLast) {
        this.page++;
        this.cargarClientes();
      }
    }

    setPage(page: number): void {
      this.page = page;
      this.cargarClientes();
    }

    setOrder(order: string): void {
      this.order = order;
      this.cargarClientes();
    }

    onChangeSize(){
      console.log(this.size)
      this.cargarClientes()
    }

}
