import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import swal from 'sweetalert2'

@Injectable({
  providedIn: 'root'
})
export class AuthInterceptorService implements HttpInterceptor {

  constructor(
    private router: Router
  ) {}

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    const token: string = localStorage.getItem('auth_token');

    let request = req;

    if (token) {
      request = req.clone({
        setHeaders: {
          Authorization: `${ token }`
        }
      });
    }

    return next.handle(request).pipe(
      catchError((err: HttpErrorResponse) => {

        if (err.status === 401) {
          this.router.navigate(['/login']);
          swal('Error', `El usuario no posee permisos`, 'error')
        }
        if (err.status == 0){ //or whatever condition you like to put
          localStorage.removeItem('auth_token');
          localStorage.removeItem('username');
          this.router.navigate(['/login']);
          swal('Error', `El servicio backend no se encuentra disponible`, 'error')
        }

        const JsonParseError = 'Http failure during parsing for';
    		const matches = err.message.match(new RegExp(JsonParseError, 'ig'));

    		if (err.status === 200 && matches.length === 1) {
    			// return obs that completes;
          localStorage.removeItem('auth_token');
          localStorage.removeItem('username');
          this.router.navigate(['/login']);
          swal('Error', `El tocken ha vencido`, 'error')
    		}

        return throwError( err );

      })
    );
  }

}
