import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { Producto } from './producto'
import { ItemPaginador } from '../utils/itemPaginador'
import { ProductoService } from './producto.service';
import swal from 'sweetalert2'

@Component({
  selector: 'app-productos',
  templateUrl: './productos.component.html'
})
export class ProductosComponent implements OnInit {

  private errores: string[];

  productos: Producto[];
  totalPages: Array<number>;
  page = 0;
  size = 5;
  order = 'codProducto';
  asc = true;
  isFirst = false;
  isLast = false;
  numerosFooterPaginador: ItemPaginador[];

constructor(private productoService: ProductoService, private router: Router) { }

ngOnInit() {
  this.cargarProductos();
}

cargarProductos(){
  this.productoService.getProductos(this.page, this.size, this.order, this.asc).subscribe(
    respuestaJson => {
      this.productos = respuestaJson.productos;
      this.isFirst = respuestaJson.paginador.first;
      this.isLast = respuestaJson.paginador.last;
      this.totalPages = new Array(respuestaJson.paginador.totalPaginas);
      this.numerosFooterPaginador = respuestaJson.paginador.paginas

    },
    err => {
      /*this.errores = err.error.errors as string[];
      console.error('Codigo de error desde el backend: '+ err.status)
      console.error(err.error.errors)*/
      console.error('Codigo de error desde el backend: '+ err.status)
    }
  )
}

delete(producto: Producto){
  swal({
  title: 'Está seguro?',
  text: `¿Seguro que desea eliminar al producto ${producto.descripcion}?`,
  type: 'warning',
  showCancelButton: true,
  confirmButtonColor: '#3085d6',
  cancelButtonColor: '#d33',
  confirmButtonText: 'Si, eliminar!',
  cancelButtonText: 'No, cancelar!',
  confirmButtonClass: 'btn btn-success',
  cancelButtonClass: 'btn btn-danger',
  buttonsStyling: false,
  reverseButtons: true
}).then((result) => {
  if (result.value) {

    this.productoService.delete(producto.id).subscribe(
      response => {
        this.productos = this.productos.filter(cli => cli !== producto)
        swal(
          'Producto Eliminado!',
          `Producto ${producto.descripcion} eliminado con éxito.`,
          'success'
        )
      }
    )

  }
})
}


  sort(): void {
    this.asc = !this.asc;
    this.cargarProductos();
  }

  rewind(): void {
      if (!this.isFirst) {
        this.page--;
        this.cargarProductos();
      }
    }

    forward(): void {
      if (!this.isLast) {
        this.page++;
        this.cargarProductos();
      }
    }

    setPage(page: number): void {
      this.page = page;
      this.cargarProductos();
    }

    setOrder(order: string): void {
      this.order = order;
      this.cargarProductos();
    }

    onChangeSize(){
      console.log(this.size)
      this.page=0
      this.cargarProductos()
    }

}
