import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { ProductosComponent } from './productos/productos.component';
import { ProductoService } from './productos/producto.service'
import { FormFacturaComponent } from './facturas/form.component';
import { FacturaService } from './facturas/factura.service'
import { ClientesComponent } from './clientes/clientes.component';
import { ClienteService } from './clientes/cliente.service'
import { AuthService } from './login/auth.service'
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FormClienteComponent } from './clientes/form.component';
import { FormProductoComponent } from './productos/form.component';
import { FormsModule } from '@angular/forms';

import { RouterModule, Routes} from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AuthGuard } from './guards/auth.guard';

import { LoaderComponent } from './utils/loader/loader.component';
import { LoaderService } from './utils/loader/loader.service';
import { LoaderInterceptor } from './utils/loader/loader-interceptor';

// Interceptors
import { AuthInterceptorService } from './login/auth-interceptor.service';
import { LibroIvaVentasComponent } from './libro-iva-ventas/libro-iva-ventas.component';



const routes: Routes = [
  {path: '', redirectTo: '/clientes', pathMatch: 'full'},
  {path: 'clientes', component: ClientesComponent, canActivate: [AuthGuard] },
  {path: 'clientes/form', component: FormClienteComponent, canActivate: [AuthGuard] },
  {path: 'clientes/form/:id', component: FormClienteComponent, canActivate: [AuthGuard] },
  {path: 'productos', component: ProductosComponent, canActivate: [AuthGuard] },
  {path: 'productos/form', component: FormProductoComponent, canActivate: [AuthGuard] },
  {path: 'productos/form/:id', component: FormProductoComponent, canActivate: [AuthGuard] },
  {path: 'facturas', component: FormFacturaComponent, canActivate: [AuthGuard] },
  {path: 'libroIvaVentas', component: LibroIvaVentasComponent, canActivate: [AuthGuard] },
  {path: 'login', component: LoginComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    ProductosComponent,
    ClientesComponent,
    FormClienteComponent,
    FormProductoComponent,
    FormFacturaComponent,
    LoginComponent,
    LibroIvaVentasComponent,
    LoaderComponent
  ],
  imports: [
    NgbModule, BrowserModule, HttpClientModule, FormsModule,
    RouterModule.forRoot(routes)
  ],
  providers: [ProductoService, ClienteService, FacturaService, AuthService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptorService,
      multi: true
    },
    LoaderService,
    { provide: HTTP_INTERCEPTORS, useClass: LoaderInterceptor, multi: true }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
