
import { Injectable } from '@angular/core';
import { Cliente } from './cliente';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { of } from 'rxjs/observable/of';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AppSettings } from '../utils/appSettings'

import swal from 'sweetalert2'
import { Router } from '@angular/router'


@Injectable()
export class ClienteService {


  private urlEndPoint: string = AppSettings.API_ENDPOINT+'/api/clientes';


  constructor(private http : HttpClient, private router: Router) { }


  getClientes(page: number, size: number, order: string, asc: boolean): Observable<any>{
    return this.http.get<any>(this.urlEndPoint+ `?page=${page}&size=${size}&order=${order}&asc=${asc}&filtro=`)
  }

  getClientesConFiltro(page: number, size: number, order: string, asc: boolean, filtro: string): Observable<any>{
    return this.http.get<any>(this.urlEndPoint+ `?page=${page}&size=${size}&order=${order}&asc=${asc}&filtro=${filtro}`)
  }

  create(cliente: Cliente) : Observable<any> {

    return this.http.post<any>(this.urlEndPoint, cliente).pipe(
      catchError(e=>{
        if(e.status==400){
          return throwError(e)
        }

        console.error(e.error.mensaje)
        swal(e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }

  getCliente(id): Observable<Cliente>{
    return this.http.get<Cliente>(`${this.urlEndPoint}/${id}`).pipe(
      catchError(e =>{
        this.router.navigate(['/clientes'])
        console.error(e.error.mensaje)
        swal('Error al editar', e.error.mensaje, 'error')
        return throwError(e)
      })
    )

  }

  update(cliente: Cliente): Observable<any>{
    return this.http.put<any>(`${this.urlEndPoint}/${cliente.id}`, cliente).pipe(
      catchError(e=>{
        if(e.status==400){
          return throwError(e)
        }

        console.error(e.error.mensaje)
        swal(e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }

  delete (id: number): Observable<Cliente>{

    return this.http.delete<Cliente>(`${this.urlEndPoint}/${id}`).pipe(
      catchError(e=>{
        console.error(e.error.mensaje)
        swal( e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }


}
