import { Component, OnInit } from '@angular/core';
import { Cliente } from './cliente';
import { ClienteService } from './cliente.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Observable, throwError } from 'rxjs';
import { ItemPaginador } from '../utils/itemPaginador'
import swal from 'sweetalert2'

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html'
})
export class FormClienteComponent implements OnInit {


  cliente: Cliente = new Cliente()
  titulo:string = "Crear cliente"

  errores: string[];

  constructor(private clienteService: ClienteService, private router: Router, private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.cargarCliente()
  }

  cargarCliente(): void{
    this.activatedRoute.params.subscribe(
      params => {
        let id = params['id']
        if(id){
          this.clienteService.getCliente(id).subscribe (
            (cliente) => this.cliente = cliente
          )
        }
      }
    )

  }

  create(): void{
    this.clienteService.create(this.cliente).subscribe(
      respuestaJson => {
        this.router.navigate(['/clientes'])
        swal('Nuevo cliente', `${respuestaJson.mensaje} : ${respuestaJson.cliente.nombre}`, 'success')
      },
      e => {
        if(e.status==401){
          console.error('ERROR 401!!!')
          return throwError(e)
        }
      }
    )
  }

  update(): void{
    this.clienteService.update(this.cliente).subscribe(
      respuestaJson => {
        this.router.navigate(['/clientes'])
        swal('Cliente actualizado', `${respuestaJson.mensaje} : ${respuestaJson.cliente.nombre}`, 'success')
      },
      err => {
        this.errores = err.error.errors as string[];
        console.error('Codigo de error desde el backend: '+ err.status)
        console.error(err.error.errors)
      }
    )
  }

}
