import { Component, OnInit } from '@angular/core';
import { FacturaService } from '../facturas/factura.service'
import { Factura } from '../facturas/factura'
import { ItemPaginador } from '../utils/itemPaginador'
import { NgbDateStruct } from "@ng-bootstrap/ng-bootstrap";


@Component({
  selector: 'app-libro-iva-ventas',
  templateUrl: './libro-iva-ventas.component.html',

})
export class LibroIvaVentasComponent implements OnInit {

  constructor(private facturaService: FacturaService) { }

  errores: string[];

  fechaDesde: NgbDateStruct
  fechaHasta: NgbDateStruct
  fechaDesdeString: any
  fechaHastaString: any
  facturas: Factura[]

  ngOnInit() {
    this.fechaDesde=null
    this.fechaHasta=null
  }

  cargarFacturas(){
    this.fechaDesdeString = ''
    this.fechaHastaString = ''

    if(this.fechaDesde && this.fechaHasta){
      if(this.fechaDesde.year && this.fechaHasta.year){
        this.fechaDesdeString = this.fechaDesde.year+'-'+this.fechaDesde.month +'-'+ this.fechaDesde.day
        this.fechaHastaString = this.fechaHasta.year+'-'+this.fechaHasta.month +'-'+ this.fechaHasta.day

        console.log(this.fechaDesdeString)
      }
    }
    this.facturaService.obtenerFacturas(this.page, this.size, this.order, this.asc, this.fechaDesdeString, this.fechaHastaString).subscribe(
      rtaJson => {
        this.facturas = rtaJson.facturas

        this.isFirst = rtaJson.paginador.first;
        this.isLast = rtaJson.paginador.last;
        this.totalPages = new Array(rtaJson.paginador.totalPaginas);
        this.numerosFooterPaginador = rtaJson.paginador.paginas

        this.errores = []
      },
      err => {
        this.errores = err.error.errors as string[];
        console.error('Codigo de error desde el backend: '+ err.status)
        console.error(err.error.errors)
      }
    )
  }

  /* PAGINADOR */
    totalPages: Array<number>;
    page = 0;
    size = 5;
    order = 'fechaCreacion';
    asc = true;
    isFirst = false;
    isLast = false;
    numerosFooterPaginador: ItemPaginador[];

    sort(): void {
      this.asc = !this.asc;
      this.cargarFacturas();
    }

    rewind(): void {
        if (!this.isFirst) {
          this.page--;
          this.cargarFacturas();
        }
      }

      forward(): void {
        if (!this.isLast) {
          this.page++;
          this.cargarFacturas();
        }
      }

      setPage(page: number): void {
        this.page = page;
        this.cargarFacturas();
      }

      setOrder(order: string): void {
        this.order = order;
        this.cargarFacturas();
      }

      onChangeSize(){
        console.log(this.size)
        this.cargarFacturas()
      }

}
