
import { Injectable } from '@angular/core';
import { Factura } from './factura';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { of } from 'rxjs/observable/of';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AppSettings } from '../utils/appSettings'

import swal from 'sweetalert2'
import { Router } from '@angular/router'


@Injectable()
export class FacturaService {


  private urlEndPoint: string = AppSettings.API_ENDPOINT+'/api/facturas';


  constructor(private http : HttpClient, private router: Router) { }

  obtenerFacturas(page: number, size: number, order: string, asc: boolean, fechaDesde: any, fechaHasta: any): Observable<any>{
    return this.http.get<any>(this.urlEndPoint+`/libroIvaVentas?page=${page}&size=${size}&order=${order}&asc=${asc}&filtroFechaDesde=${fechaDesde}&filtroFechaHasta=${fechaHasta}`)
  }

  obtenerProximoNroFactura(tipoComprobante: string, claseComprobante: string): Observable<any>{

    return this.http.get<any>(this.urlEndPoint+`/obtenerProximoNroFactura?tipoComprobante=${tipoComprobante}&claseComprobante=${claseComprobante}`)
  }

  create(factura: Factura) : Observable<any> {


    return this.http.post<any>(this.urlEndPoint, factura).pipe(
      catchError(e=>{
        if(e.status==400){
          let errores: string[]
          console.error(e.errors)
          swal('Error de solicitud', 'Solucionar errores mencionados en rojo', 'error')
          return throwError(e)
        }
        console.error(e.error.mensaje)
        swal(e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }

  getFactura(id): Observable<Factura>{


    return this.http.get<Factura>(`${this.urlEndPoint}/${id}`).pipe(
      catchError(e =>{
        this.router.navigate(['/facturas'])
        console.error(e.error.mensaje)
        swal('Error al editar', e.error.mensaje, 'error')
        return throwError(e)
      })
    )

  }

  update(factura: Factura): Observable<any>{


    return this.http.put<any>(`${this.urlEndPoint}/${factura.idFactura}`, factura ).pipe(
      catchError(e=>{
        if(e.status==400){
          return throwError(e)
        }

        console.error(e.error.mensaje)
        swal(e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }

  delete (id: number): Observable<Factura>{


    return this.http.delete<Factura>(`${this.urlEndPoint}/${id}` ).pipe(
      catchError(e=>{
        console.error(e.error.mensaje)
        swal( e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }


}
