
import { Injectable } from '@angular/core';
import { Producto } from './producto';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { of } from 'rxjs/observable/of';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AppSettings } from '../utils/appSettings'

import swal from 'sweetalert2'
import { Router } from '@angular/router'


@Injectable()
export class ProductoService {

  private urlEndPoint: string = AppSettings.API_ENDPOINT+'/api/productos';


  constructor(private http : HttpClient, private router: Router) { }


  getProductos(page: number, size: number, order: string, asc: boolean): Observable<any>{

    return this.http.get<any>(this.urlEndPoint+ `?page=${page}&size=${size}&order=${order}&asc=${asc}`)
  }

  getProductosConFiltro(page: number, size: number, order: string, asc: boolean, filtro: string): Observable<any>{

    return this.http.get<any>(this.urlEndPoint+ `?page=${page}&size=${size}&order=${order}&asc=${asc}&filtro=${filtro}`)
  }

  create(producto: Producto) : Observable<any> {

    return this.http.post<any>(this.urlEndPoint, producto).pipe(
      catchError(e=>{
        if(e.status==400){
          return throwError(e)
        }

        console.error(e.error.mensaje)
        swal(e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }

  getProducto(id): Observable<any>{
    return this.http.get<any>(`${this.urlEndPoint}/${id}`).pipe(
      catchError(e =>{
        this.router.navigate(['/productos'])
        console.error(e.error.mensaje)
        swal('Error al editar', e.error.mensaje, 'error')
        return throwError(e)
      })
    )

  }

  update(producto: Producto): Observable<any>{
    return this.http.put<any>(`${this.urlEndPoint}/${producto.id}`, producto).pipe(
      catchError(e=>{
        if(e.status==400){
          return throwError(e)
        }

        console.error(e.error.mensaje)
        swal(e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }

  delete (id: number): Observable<Producto>{
    return this.http.delete<Producto>(`${this.urlEndPoint}/${id}` ).pipe(
      catchError(e=>{
        console.error(e.error.mensaje)
        swal( e.error.mensaje, e.error.error, 'error')
        return throwError(e)
      })
    )
  }


}
