import { Component, OnInit } from '@angular/core';
import { Producto } from './producto';
import { ProductoService } from './producto.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Observable, throwError } from 'rxjs';
import swal from 'sweetalert2'

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html'
})
export class FormProductoComponent implements OnInit {


  producto: Producto = new Producto()
  titulo:string = "Crear producto"

  errores: string[];

  constructor(private productoService: ProductoService, private router: Router, private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.cargarProducto()
  }

  cargarProducto(): void{
    this.activatedRoute.params.subscribe(
      params => {
        let id = params['id']
        if(id){
          this.productoService.getProducto(id).subscribe (
            rta =>{
              this.producto = rta.producto
            }
          )
        }
      }
    )

  }

  create(): void{
    this.productoService.create(this.producto).subscribe(
      respuestaJson => {
        this.router.navigate(['/productos'])
        swal('Nuevo producto', `${respuestaJson.mensaje} : ${respuestaJson.producto.descripcion}`, 'success')
      },
      e => {
        if(e.status==401){
          console.error('ERROR 401!!!')
          return throwError(e)
        }
      }
    )
  }

  update(): void{
    this.productoService.update(this.producto).subscribe(
      respuestaJson => {
        this.router.navigate(['/productos'])
        swal('Producto actualizado', `${respuestaJson.mensaje} : ${respuestaJson.producto.descripcion}`, 'success')
      },
      err => {
        this.errores = err.error.errors as string[];
        console.error('Codigo de error desde el backend: '+ err.status)
        console.error(err.error.errors)
      }
    )
  }

}
